___CSS_LOADER_EXPORT___.push([module.id, ".full-width,.my-table{width:100%}.my-table,.my-td{border:1px solid #ccc}.my-td{padding:2px}.opened{background-color:#02fffb}.row-centered{text-align:center}.col-centered{display:inline-block;float:none;margin-right:-4px;text-align:left}.pos{position:top;top:240px}.v-enter-active,.v-leave-active{transition:opacity .5s ease}.v-enter-from,.v-leave-to{opacity:0}.bounce-enter-active{animation:bounce-in .5s}.bounce-leave-active{animation:bounce-in .5s reverse}@keyframes bounce-in{0%{transform:scale(0)}50%{transform:scale(1.1)}to{transform:scale(1)}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/SummaryOfRating/Index.vue"],"names":[],"mappings":"AA6WA,sBAJI,UAOJ,CAEA,iBAHI,qBAMJ,CAHA,OACI,WAEJ,CAEA,QACI,wBACJ,CAGA,cACI,iBACJ,CAEA,cACI,oBAAqB,CACrB,UAAW,CAEX,iBAAkB,CADlB,eAEJ,CAEA,KACI,YAAa,CACb,SACJ,CAGA,gCAEI,2BACJ,CAEA,0BAEI,SACJ,CAGA,qBACI,uBACJ,CAEA,qBACI,+BACJ,CAEA,qBACI,GACI,kBACJ,CAEA,IACI,oBACJ,CAEA,GACI,kBACJ,CAGJ","sourcesContent":["<template>\r\n\r\n    <Head>\r\n        <title>Home</title>\r\n    </Head>\r\n\r\n    <!--<p style=\"text-align: justify;\">Sed ut perspiciatis, unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam eaque ipsa, quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt, explicabo. Nemo enim ipsam voluptatem, quia voluptas sit, aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos, qui ratione voluptatem sequi nesciunt, neque porro quisquam est, qui dolorem ipsum, quia dolor sit amet consectetur.\r\n    </p>-->\r\n    <div class=\"row gap-20 masonry pos-r\">\r\n        <div class=\"peers fxw-nw jc-sb ai-c\">\r\n            <!--SEMESTRAL***************************************************************************************-->\r\n            <h3>Summary of Ratings</h3>\r\n            <div class=\"peers\">\r\n                <!-- <div class=\"peer mR-10\">\r\n                    <input v-model=\"search\" type=\"text\" class=\"form-control form-control-sm\" placeholder=\"Search...\">\r\n                </div> -->\r\n                <!-- <div class=\"peer\"> -->\r\n                <!-- /ipcrsemestral/create/{{ id }}/semestral {{ source }} -->\r\n                <!-- <Link class=\"btn btn-primary btn-sm\" :href=\"`/ipcrsemestral/create/${id}/${source}`\">Add IPCR </Link>\r\n                </div> -->\r\n                <Link v-if=\"source !== 'direct'\" :href=\"`/employees`\">\r\n                <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"25\" height=\"25\" fill=\"currentColor\" class=\"bi bi-x-lg\"\r\n                    viewBox=\"0 0 16 16\">\r\n                    <path fill-rule=\"evenodd\"\r\n                        d=\"M13.854 2.146a.5.5 0 0 1 0 .708l-11 11a.5.5 0 0 1-.708-.708l11-11a.5.5 0 0 1 .708 0Z\" />\r\n                    <path fill-rule=\"evenodd\"\r\n                        d=\"M2.146 2.146a.5.5 0 0 0 0 .708l11 11a.5.5 0 0 0 .708-.708l-11-11a.5.5 0 0 0-.708 0Z\" />\r\n                </svg>\r\n                </Link>\r\n                <!-- <button class='btn btn-primary text-white' @click=\"generateIPCR()\">Generate IPCR</button> -->\r\n            </div>\r\n\r\n        </div>\r\n        <div>\r\n            <div><b>Employee Name: </b><u>{{ emp.employee_name }}</u></div>\r\n            <div><b>Position: </b><u>{{ emp.position_long_title }}</u></div>\r\n            <div><b>Division: </b><u>{{ division }}</u></div>\r\n        </div>\r\n\r\n        <div class=\"masonry-sizer col-md-6\"></div>\r\n        <div class=\"masonry-item w-100\">\r\n            <div class=\"row gap-20\"></div>\r\n            <div class=\"bgc-white p-20 bd\">\r\n                <div class=\"table-responsive\">\r\n                    <!--table-borderless table-striped -->\r\n                    <table class=\"table table-sm table-hover table-borderless table-striped\">\r\n                        <thead>\r\n                            <tr style=\"background-color: #B7DEE8;\">\r\n                                <th>&nbsp;&nbsp;</th>\r\n                                <th>Semester</th>\r\n                                <th>Period</th>\r\n                                <!-- <th>Actions</th> -->\r\n                            </tr>\r\n                        </thead>\r\n                        <tbody>\r\n                            <template v-for=\"(sem, index) in sem_data\" :key=\"index\">\r\n                                <tr :class=\"{ opened: opened.includes(sem.id) }\" @click=\"toggle(sem.id, index)\"\r\n                                    style=\"cursor: pointer\">\r\n                                    <td>\r\n                                        <a class=\"dropdown-toggle\" href=\"javascript:void(0);\">\r\n                                            <span class=\"icon-holder\">\r\n                                                <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"16\" height=\"16\"\r\n                                                    fill=\"currentColor\" class=\"bi bi-clipboard-check-fill\"\r\n                                                    viewBox=\"0 0 16 16\">\r\n                                                    <path\r\n                                                        d=\"M6.5 0A1.5 1.5 0 0 0 5 1.5v1A1.5 1.5 0 0 0 6.5 4h3A1.5 1.5 0 0 0 11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3Zm3 1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1-.5-.5v-1a.5.5 0 0 1 .5-.5h3Z\" />\r\n                                                    <path\r\n                                                        d=\"M4 1.5H3a2 2 0 0 0-2 2V14a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2V3.5a2 2 0 0 0-2-2h-1v1A2.5 2.5 0 0 1 9.5 5h-3A2.5 2.5 0 0 1 4 2.5v-1Zm6.854 7.354-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 0 1 .708-.708L7.5 10.793l2.646-2.647a.5.5 0 0 1 .708.708Z\" />\r\n                                                </svg>\r\n                                            </span>\r\n                                            <span class=\"arrow\">\r\n                                            </span>\r\n                                        </a>\r\n                                    </td>\r\n                                    <td>\r\n                                        {{ getSemester(sem.sem) }}\r\n                                    </td>\r\n                                    <td>\r\n                                        {{ getPeriod(sem.sem, sem.year) }}\r\n                                    </td>\r\n                                </tr>\r\n                                <tr v-if=\"opened.includes(sem.id)\">\r\n                                    <td colspan=\"6\" class=\"background-white\">\r\n                                        <!---->\r\n                                        <Transition name=\"bounce\">\r\n                                            <!-- v-if=\"show\" -->\r\n                                            <p v-if=\"show[index]\">\r\n                                            <table class=\"table-responsive full-width\">\r\n                                                <tbody>\r\n                                                    <tr>\r\n                                                        <th></th>\r\n                                                        <th class=\"text-white text-center \"\r\n                                                            style=\"background-color: #727272;\" colspan=\"2\">\r\n                                                            <h6>&nbsp;&nbsp;Summary of Ratings</h6>\r\n                                                        </th>\r\n                                                        <th></th>\r\n                                                    </tr>\r\n                                                </tbody>\r\n                                                <tbody>\r\n                                                    <tr>\r\n                                                        <td rowspan=\"2\"></td>\r\n                                                        <th class=\"my-td text-center text-white\"\r\n                                                            style=\"background-color: #92a2a2;\" rowspan=\"2\">\r\n                                                            &nbsp;&nbsp;PERIOD\r\n                                                        </th>\r\n\r\n                                                        <th class=\"my-td text-center text-white\"\r\n                                                            style=\"background-color: #92a2a2;\" colspan=\"1\">ACTIONS</th>\r\n                                                        <td rowspan=\"2\"></td>\r\n                                                    </tr>\r\n                                                    <tr>\r\n                                                        <th class=\"my-td text-center text-white\"\r\n                                                            style=\"background-color: #727272;\">VIEW</th>\r\n                                                    </tr>\r\n                                                </tbody>\r\n                                                <tbody>\r\n                                                    <tr v-for=\"my_sem in sem.monthly_accomplishment\">\r\n                                                        <td>&nbsp;&nbsp;&nbsp;\r\n                                                            <!-- {{ my_sem }} -->\r\n                                                        </td>\r\n                                                        <td class=\"my-td text-center\">&nbsp;&nbsp;{{\r\n                                                            getMonthName(my_sem.month) }}, {{ my_sem.year }}</td>\r\n\r\n                                                        <td class=\"my-td text-center\">\r\n                                                            <button\r\n                                                                @click=\"JanuaryAccomplishment(getMonthName(my_sem.month), sem.year, my_sem.ipcr_semestral_id, sem.department_code)\"\r\n                                                                class=\"btn btn-primary text-white\">\r\n                                                                View\r\n                                                            </button>\r\n                                                        </td>\r\n                                                        <td>&nbsp;&nbsp;&nbsp;&nbsp;</td>\r\n                                                    </tr>\r\n                                                    <tr>\r\n                                                        <td>&nbsp;&nbsp;&nbsp;</td>\r\n                                                        <td class=\"my-td text-center\">&nbsp;&nbsp;\r\n                                                            {{ getPeriod(sem.sem, sem.year) }}\r\n                                                        </td>\r\n\r\n\r\n                                                        <td class=\"my-td text-center\">\r\n                                                            <button\r\n                                                                @click=\"SemesterRating(sem.year, sem.department_code, sem.sem)\"\r\n                                                                class=\"btn btn-primary text-white\">\r\n                                                                View\r\n                                                            </button>\r\n                                                        </td>\r\n                                                    </tr>\r\n                                                </tbody>\r\n                                            </table>\r\n                                            </p>\r\n                                        </Transition>\r\n                                    </td>\r\n                                </tr>\r\n                            </template>\r\n                        </tbody>\r\n                    </table>\r\n                    <pagination :next=\"sem_data.next_page_url\" :prev=\"sem_data.prev_page_url\" />\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <Modal v-if=\"displayModal\" @close-modal-event=\"hideModal\">\r\n            <div class=\"d-flex justify-content-center\">\r\n                <h4>{{ modal_title }}</h4>\r\n            </div>\r\n        </Modal>\r\n        <div v-if=\"show\">show</div>\r\n    </div>\r\n</template>\r\n<script>\r\nimport Filtering from \"@/Shared/Filter\";\r\nimport Pagination from \"@/Shared/Pagination\";\r\nimport Modal from \"@/Shared/PrintModal\";\r\nexport default {\r\n    props: {\r\n        month: Object,\r\n        data: Object,\r\n        MOOE: String,\r\n        PS: String,\r\n        id: String,\r\n        emp: Object,\r\n        division: Object,\r\n        source: String,\r\n        sem_data: Object,\r\n        shown_id: String\r\n    },\r\n    mounted() {\r\n        // alert(this.shown_id)\r\n    },\r\n    data() {\r\n        return {\r\n            my_link: \"\",\r\n            displayModal: false,\r\n            modal_title: \"Add\",\r\n            opened: [],\r\n            sem1: ['January', 'February', 'March', 'April', 'May', 'June'],\r\n            sem2: ['July', 'August', 'September', 'October', 'November', 'December'],\r\n            // show: false,\r\n            show: [],\r\n            //search: this.$props.filters.search,\r\n        }\r\n    },\r\n    watch: {\r\n        search: _.debounce(function (value) {\r\n            this.$inertia.get(\r\n                \"/paps/\" + this.idmfo,\r\n                { search: value },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        }, 300),\r\n    },\r\n    components: {\r\n        Pagination, Filtering, Modal,\r\n    },\r\n    mounted() {\r\n\r\n        this.setShow()\r\n    },\r\n    methods: {\r\n        deleteIPCR(ipcr_id) {\r\n            let text = \"WARNING!\\nAre you sure you want to delete this IPCR?\";\r\n            if (confirm(text) == true) {\r\n                this.$inertia.delete(\"/ipcrsemestral/delete/\" + ipcr_id + '/' + this.source);\r\n            }\r\n        },\r\n        submitIPCR(ipcr_id) {\r\n            // alert(ipcr_id);\r\n            let text = \"WARNING!\\nAre you sure you want to submit this IPCR?\";\r\n            if (confirm(text) == true) {\r\n                this.$inertia.post(\"/ipcrsemestral/submit/\" + ipcr_id + '/' + this.source);\r\n            }\r\n        },\r\n        showCreate() {\r\n            this.$inertia.get(\r\n                \"/targets/create\",\r\n                {\r\n                    raao_id: this.raao_id\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        deletePAPS(id) {\r\n            let text = \"WARNING!\\nAre you sure you want to delete the Program and Projects? \" + id;\r\n            if (confirm(text) == true) {\r\n                this.$inertia.delete(\"/paps/\" + id + \"/\" + this.idmfo);\r\n            }\r\n        },\r\n        getToRep(ffunccod, ffunction, MOOE, PS) {\r\n            // alert(data[0].FFUNCCOD);\r\n            var linkt = \"http://\";\r\n            var jasper_ip = this.jasper_ip;\r\n            var jasper_link = 'jasperserver/flow.html?pp=u%3DJamshasadid%7Cr%3DManager%7Co%3DEMEA,Sales%7Cpa1%3DSweden&_flowId=viewReportFlow&_flowId=viewReportFlow&_flowId=viewReportFlow&ParentFolderUri=%2Freports%2Fplanning_system%2FOPCR_Standard&reportUnit=%2Freports%2Fplanning_system%2FOPCR_Standard%2FOPCR&standAlone=true&decorate=no&output=pdf';\r\n            var params = '&id=' + ffunccod + '&FUNCTION=' + ffunction + '&MOOE=' + MOOE + '&PS=' + PS;\r\n            var link1 = linkt + jasper_ip + jasper_link + params;\r\n            return link1;\r\n        },\r\n        showModal(title_pass, emp_id) {\r\n            //this.my_link = this.getToRep(ffunccod, ffunction, MOOE, PS);\r\n            if (title_pass === \"add\") {\r\n                this.modal_title = \"Add\";\r\n            } else {\r\n                this.modal_title = \"Edit\";\r\n            }\r\n            this.displayModal = true;\r\n\r\n        },\r\n        hideModal() {\r\n            this.displayModal = false;\r\n        },\r\n        JanuaryAccomplishment(month, year, ipcr_semestral_id, department_code) {\r\n            console.log(department_code)\r\n            this.$inertia.get(\r\n                \"/summary-rating/monthly\",\r\n                {\r\n                    month: month,\r\n                    year: year,\r\n                    ipcr_semestral_id: ipcr_semestral_id,\r\n                    department_code: department_code\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n\r\n        SemesterRating(year, department_code, sem) {\r\n            console.log(department_code)\r\n            this.$inertia.get(\r\n                \"/semester-rating/semester\",\r\n                {\r\n                    year: year,\r\n                    department_code: department_code,\r\n                    sem: sem\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        setShow() {\r\n            for (var x = 0; x < this.sem_data.length; x++) {\r\n                this.show.push(false);\r\n            }\r\n        },\r\n        toggle(id, i) {\r\n            // alert(this.sem_data.length)\r\n            const index = this.opened.indexOf(id);\r\n            if (index > -1) {\r\n                // this.opened.splice(index, 1)\r\n            } else {\r\n                this.opened = [];\r\n                this.opened.push(id)\r\n            }\r\n            // alert(this.show);\r\n            setTimeout(() => {\r\n                // alert(this.show);\r\n                // this.show = !this.show;\r\n                for (var t = 0; t < this.sem_data.length; t++) {\r\n                    if (i != t) {\r\n                        this.show[t] = false\r\n                    }\r\n                }\r\n                this.show[i] = !this.show[i];\r\n            }, 100);\r\n        },\r\n        submitMonthlyAccomplishment(my_id, id_shown) {\r\n            // alert(id)\r\n            let text = \"WARNING!\\nAre you sure you want to submit this Monthly Accomplishment? \";\r\n            if (confirm(text) == true) {\r\n                const params = {\r\n                    id_shown: id_shown\r\n                };\r\n                const url = '/monthly-accomplishment/submit/monthly/accomplishment/' + my_id;\r\n                // axios.get(url);\r\n                this.$inertia.get(url, params, {\r\n                    preserveState: true,\r\n                });\r\n            }\r\n\r\n        },\r\n        generateIPCR() {\r\n            const url = '/monthly-accomplishment/generate/monthly';\r\n            axios.get(url);\r\n        }\r\n    }\r\n};\r\n</script>\r\n<style>\r\n/***TABLE FULL WIDTH */\r\n.full-width {\r\n    width: 100%;\r\n}\r\n\r\n/**ACCORDION BEGIN*********************/\r\n.my-table {\r\n    width: 100%;\r\n    border: 1px solid #ccc;\r\n}\r\n\r\n.my-td {\r\n    padding: 2px;\r\n    border: 1px solid #ccc;\r\n}\r\n\r\n.opened {\r\n    background-color: rgb(2, 255, 251);\r\n}\r\n\r\n/**ACCORDION END*********************/\r\n.row-centered {\r\n    text-align: center;\r\n}\r\n\r\n.col-centered {\r\n    display: inline-block;\r\n    float: none;\r\n    text-align: left;\r\n    margin-right: -4px;\r\n}\r\n\r\n.pos {\r\n    position: top;\r\n    top: 240px;\r\n}\r\n\r\n/*TOGGLE FADE TRANSITION*/\r\n.v-enter-active,\r\n.v-leave-active {\r\n    transition: opacity 0.5s ease;\r\n}\r\n\r\n.v-enter-from,\r\n.v-leave-to {\r\n    opacity: 0;\r\n}\r\n\r\n/* transition */\r\n.bounce-enter-active {\r\n    animation: bounce-in 0.5s;\r\n}\r\n\r\n.bounce-leave-active {\r\n    animation: bounce-in 0.5s reverse;\r\n}\r\n\r\n@keyframes bounce-in {\r\n    0% {\r\n        transform: scale(0);\r\n    }\r\n\r\n    50% {\r\n        transform: scale(1.1);\r\n    }\r\n\r\n    100% {\r\n        transform: scale(1);\r\n    }\r\n\r\n\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
